@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro|Work+Sans:300,400&display=swap);
/* Google Material Icon Fonts */
/* https://www.material.io/tools/icons/ */
/* https://google.github.io/material-design-icons/ */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* ADAPTIVE TEXT SIZES */

@media only screen and (max-height: 600px), (max-width: 800px) {
  body {
    font-size: 0.85em;
  }
}

@media only screen and (min-height: 1000px) and (min-width: 1000px) {
  body {
    font-size: 1.15em;
  }
}

.code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.app-boundary-container {
  align-items: center;
  display: grid;
  justify-items: center;
}

.app-boundary-text{
  font-size: 2em;
  margin: 0;
  font-weight: bold;
}
.document-header-copy-button {
  cursor: pointer;
  color: #888888;
  -webkit-margin-start: 0.5em;
          margin-inline-start: 0.5em;
  -webkit-margin-end: 0.5em;
          margin-inline-end: 0.5em;
  font-size: 18pt;
}

.document-header-copy-button:active {
  opacity: 0.5;
}

.document-h1 {
  margin: 0;
  margin-bottom: 0.5em;
  font-size: 2.15em;
  font-weight: bold;
}

.document-h2 {
  margin: 0;
  margin: 0.5em 0;
  font-size: 1.825em;
  font-weight: bold;
}

.document-body {
  font-size: 1.325em;
  line-height: 150%;
  margin: 0;
  margin-bottom: 0.25em;
}

.document-code {
  background-color: #ff2b8011;
  border-radius: 0.25em;
  color: #ff2b80;
  font-size: 0.78em;
  font-style: normal;
  padding: 0.1em 0.25em 0.1em 0.25em;
}

.document-code-link {
  cursor: pointer;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  -webkit-text-decoration-color: #ff2b80;
          text-decoration-color: #ff2b80;
}

.document-table {
  border: solid 1px #777777;
  border-collapse: collapse;
  margin-top: 1em;
  margin-bottom: 1em;
  /* width: 100%; */
}

.document-thead {
  /* background-color: #dddddd; */
}

.document-td {
  vertical-align: top;
}

.react-kawaii-promo-text {
  font-size: 0.85em;
}

.loader-container {
  align-items: center;
  display: grid;
  height: 90vh;
  justify-items: center;
  width: 100vw;
}

.loader-subcontainer {
  display: grid;
  grid-gap: 1em;
  gap: 1em;
  justify-items: center;
}

.loader-text {
  -webkit-animation-name: smooth-blinking;
          animation-name: smooth-blinking;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  color: #888888;
  font-size: 2em;
  font-weight: bold;
  text-align: center;
  margin: 0;
}

@-webkit-keyframes smooth-blinking {
  0% {
    opacity: 0;
  }
  70% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes smooth-blinking {
  0% {
    opacity: 0;
  }
  70% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.langmenu-container {
  display: grid;
}

.langmenu-item-container {
  cursor: pointer;
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 0.5em;
  gap: 0.5em;
  padding: 1em;
  -webkit-transition-duration: 0.15s;
          transition-duration: 0.15s;
}

.langmenu-item-container:active {
  opacity: 0.5;
}

.langmenu-item-container:hover {
  background-color: #88888822;
}

.langmenu-item-tick-container {
  align-items: center;
  display: grid;
  font-size: 14pt;
  justify-items: center;
  width: 24pt;
}

.langmenu-item-text-container {
  display: grid;
  grid-template-rows: auto auto;
}

.langmenu-item-displayName {
  font-size: 12pt;
  font-weight: bold;
}

.langmenu-item-nativeName {
  font-size: 12pt;
}

.navbar-container {
  display: grid;
  position: fixed;
  top: 0%;
  width: 100vw;
  overflow-x: scroll;
  overflow-y: hidden;
  z-index: 1;
}

.navbar-navlink-container {
  display: grid;
  grid-template-rows: 1fr auto;
  text-decoration: none;
  -webkit-transition-duration: 0.2s;
          transition-duration: 0.2s;
}

.navbar-navlink-container:hover {
  background-color: #ddeeff22;
  -webkit-transition-duration: 0.1s;
          transition-duration: 0.1s;
}

.navbar-navlink-container:focus {
  background-color: #ddeeff22;
}

.navbar-navlink-container:active {
  background-color: #00000022;
  /* background-color: #094a5e; */
  opacity: 1;
}

.navbar-navlink-button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background-color: transparent;
  border: none;
  margin: 0;
  padding: 0;
  outline: none;
}

.navbar-navlink-button-text {
  /* display: grid; */
  color: #ffffff;
  font-size: 24px;
}

.navbar-navlink-img {
  height: 24px;
  width: 24px;
}

.navbar-squareitem-container {
  align-items: center;
  cursor: pointer;
  display: grid;
  justify-items: center;
}

.navbar-logo-container {
  align-items: center;
  display: grid;
  grid-column-gap: 0.5em;
  column-gap: 0.5em;
  justify-items: center;
  padding: 0 1em 0 1em;
  -moz-column-gap: 0.5em;
  -webkit-column-gap: 0.5em;
}

.navbar-logo-img {
  height: 28px;
}

.navbar-logo-text {
  color: white;
  font-size: 1.5em;
  margin: 0;
  margin-bottom: 0.1em;
  font-family: "Work Sans", sans-serif;
}

.navbar-version-num {
  color: #ddeeff;
  font-size: 0.75em;
  margin: 0;
  margin-bottom: 1em;
  -webkit-margin-start: 0.5em;
          margin-inline-start: 0.5em;
}

.navbar-navlink-text {
  align-items: center;
  color: #ffffff;
  display: grid;
  font-size: 1.1em;
  justify-items: center;
  font-weight: 300;
  padding: 0 1em 0 1em;
}

.navbar-navlink-indicator {
  -webkit-transition-duration: 0.15s;
          transition-duration: 0.15s;
}

.navbar-menu-container {
  min-width: 250px;
  max-height: 75vh;
  overflow: hidden;
  overflow-y: scroll;
  position: fixed;
  z-index: 1;
}

.navbar-menu-underlay {
  height: 100%;
  position: fixed;
  width: 100%;
  z-index: 1;
}

.navbar-new-item {
  -webkit-animation-name: newItem;
          animation-name: newItem;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

@-webkit-keyframes newItem {
  0% {
    background-color: transparent;
  }
  50% {
    background-color: #ffffff44;
  }
  100% {
    background-color: transparent;
  }
}

@keyframes newItem {
  0% {
    background-color: transparent;
  }
  50% {
    background-color: #ffffff44;
  }
  100% {
    background-color: transparent;
  }
}

.footer-elem {
  color: #eeeeee;
  display: grid;
  position: absolute;
  width: 100vw;
}

.footer-container {
  display: grid;
  grid-gap: 1em;
  gap: 1em;
  grid-template-rows: 1fr auto;
  padding: 1em;
}

.footer-upper-container {
  display: grid;
  grid-gap: 1em;
  gap: 1em;
  justify-items: center;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.footer-upper-column-container {
  align-items: flex-start;
  justify-items: center;
  padding: 1em 2em 1em 2em;
  display: grid;
  grid-gap: 1em;
  gap: 1em;
}

.footer-upper-column-title {
  color: #ffeedd66;
  font-size: 11pt;
  font-weight: bold;
  margin: 0;
  margin-bottom: 0.5em;
}

.footer-lower-container {
  display: grid;
}

.footer-copyright-text {
  margin: 0;
  font-size: 11pt;
  text-align: center;
}

.footer-links {
  color: #ffeedd;
  font-size: 12pt;
}

